import Observable from '../framework/observable.js';
import { UpdateType } from '../const.js';
import { newPointButton } from '../main.js';

export default class PointsModel extends Observable {
  #points = [];
  #destinations = [];
  #offers = [];
  #pointsApiService = null;
  #isLoading = true;
  #isLoadingFailed = false;

  constructor({pointsApiService}) {

    super();
    this.#pointsApiService = pointsApiService;
  }

  async init() {
    newPointButton.disabled = true;
    // в этом методе проихсодит непосредственное взаимодействие с сервером. получаем promise
    try {
      const points = await this.#pointsApiService.points; //обращаемся к PAS и получаем список точек
      this.#points = points.map(this.#adaptToClient); // преобразовываем точки к нужному для нас виду и сохраняем в массиве points
      this.#destinations = await this.#pointsApiService.destinations;
      this.#offers = await this.#pointsApiService.offers;
      this.#isLoading = false;
      this.#isLoadingFailed = false;


    } catch(err) {

      this.#points = [];
      this.#destinations = [];
      this.#offers = [];

      this.#isLoading = false;
      this.#isLoadingFailed = true;

    }
    this._notify(UpdateType.INIT); // уведомляет всех подписчиков о поступлении данных с сервера

  }

  get points() {
    return this.#points;
  }

  get destinations() {
    return this.#destinations;
  }

  get offers() {
    return this.#offers;
  }

  get loading() {
    return this.#isLoading;
  }

  get loadingFailed() {
    return this.#isLoadingFailed;
  }

  async updatePoint (updateType, update) {
    const index = this.#points.findIndex((point) => point.id === update.id);
    if (index === -1) {
      throw new Error ('Can\'t update unexisting point');
    }
    try {
      const response = await this.#pointsApiService.updatePoint(update);
      const updatedPoint = this.#adaptToClient(response);

      this.#points = [
        ...this.#points.slice(0, index),
        update,
        ...this.#points.slice(index + 1),
      ];

      this._notify(updateType, updatedPoint);

    } catch (err) {
      throw new Error ('Can\'t update point');
    }
  }

  async addPoint(updateType, update) {
    try {
      const response = await this.#pointsApiService.addPoint(update);
      const newPoint = this.#adaptToClient(response);
      this.#points = [newPoint, ...this.#points];
      this._notify(updateType, update);
    } catch (err) {
      throw new Error('Can\'t add new point');
    }
  }

  async deletePoint(updateType, update) {
    const index = this.#points.findIndex((point) => point.id === update.id);

    if (index === -1) {
      throw new Error ('Can\'t delete unexisting point');
    }

    try {
      await this.#pointsApiService.deletePoint(update);

      this.#points = [
        ...this.#points.slice(0, index),
        ...this.#points.slice(index + 1),
      ];

      this._notify(updateType);
    } catch(err) {
      throw new Error ('Can\'t delete point');
    }
  }

  #adaptToClient(point) {
    const adaptedPoint = {
      ...point,
      basePrice: point['base_price'],
      dateFrom: point['date_from'],
      dateTo: point['date_to'],
      isFavorite: point['is_favorite'],
    };

    delete adaptedPoint['base_price'];
    delete adaptedPoint['date_from'];
    delete adaptedPoint['date_to'];
    delete adaptedPoint['is_favorite'];

    return adaptedPoint;


  }
}
